@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
@font-face {
    font-family: didot-italic;
    src: url(../../static/media/DidotLTStd-Italic.f0ec071a.otf);
}

@font-face {
    font-family: didot-regular;
    src: url("../../static/media/Didot Regular.251039e6.ttf");
}

body {
    margin: 0;
    font-family: didot-regular, sans-serif;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

.btn {
    border: none;
    cursor: pointer;
    transform: 0.3s ease;
}
.btn-black {
    background: black;
    color: white;
    padding: 8px 56px;
    border: 2px solid black;
    font-size: 1.1rem;
}
.btn-black:hover {
    background: transparent;
    color: black;
}
.btn-yellow {
    background-color: #f9d982;
    padding: 17px 20px;
    font-weight: 600;
    color: black;
}

.home-cta-section {
    display: flex;
}
.col-50 {
    flex: 1 1;
    display: flex;
}
.col-25 {
    flex: 1 1;
    display: flex;
}
.home-cta__btn,
.home-cta__btn:hover {
    width: 100%;
    color: white;
    text-decoration: none;
    transition: opacity 0.5s ease;
    padding: 0 3rem;
}
.home-cta__btn--one {
    background-color: #706c60;
}
.home-cta__btn--two {
    background-color: #baa798;
}
.home-cta__btn--three {
    background-color: #b9afa7;
}
.home-cta__btn--one:hover,
.home-cta__btn--two:hover,
.home-cta__btn--three:hover {
    opacity: 0.7;
}
/*  TEXT STYLING    */
.home-cta__title {
    font-size: 1.8rem;
    margin: 2.5rem 0 0 0;
    display: block;
}
.home-cta__subtitle {
    margin: 0 0 2.5rem 0;
    font-size: 0.9rem;
    font-weight: 100;
    display: block;
}
.cta-arrow {
    display: inline-block;
    margin-left: 0.5rem;
    transform: scale(0.9);
}

@media (max-width: 768px) {
    .home-cta__btn {
        padding: 0 2rem;
    }
    .home-cta__title {
        font-size: 1.25rem;
    }
}

@media (max-width: 576px) {
    .home-cta-section {
        flex-direction: column;
    }
    .home-cta__btn {
        padding: 0 1rem;
    }
    .home-cta__title {
        font-size: 1rem;
        margin-top: 1.5rem;
    }
    .home-cta__subtitle {
        margin-bottom: 1.5rem;
        font-weight: 300;
        font-size: 12px;
    }
}

.blog {
    padding: 1rem 3rem;
}
.blog__header {
    font-size: 1.45rem;
    text-transform: uppercase;
    padding-bottom: 10px;
    border-bottom: 1px solid lightgray;
    letter-spacing: 1px;
    margin-bottom: 0;
    font-family: didot-italic;
}

.blog__section {
    display: flex;
}
.blog__article {
    flex: 1 1;
    padding: 1rem 0;
    display: flex;
}
.blog__left img {
    transition: hover 0.3s ease;
    cursor: pointer;
}
.blog__left img:hover {
    opacity: 0.7;
}
.blog__right {
    padding-left: 1.25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.blog__title {
    font-size: 1.3rem;
    font-weight: 400;
}
.blog__desc {
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.7);
    line-height: 1.4;
}
.blog__btn {
    padding-bottom: 3px;
}
.blog__btn a {
    color: black;
    font-weight: 700;
}
.blog__cta {
    display: flex;
    justify-content: center;
    padding-top: 1.5rem;
}

@media (max-width: 768px) {
    .blog__section {
        flex-direction: column;
    }
    .blog {
        padding: 0 2rem;
    }
    .blog__left {
        flex: 2 1;
    }
    .blog__left img {
        width: 100%;
    }
    .blog__right {
        flex: 4 1;
        padding-left: 1rem;
        padding-right: 1rem;
    }
}
@media (max-width: 576px) {
    .blog__desc {
        display: none;
    }
    .blog {
        padding: 0 1rem;
    }
    .blog__header {
        font-size: 1.25rem;
    }
    .blog__right {
        padding-right: 0;
    }
}

.search-section {
    padding: 1.5rem 3rem;
    display: flex;
    justify-content: center;
    font-family: "Roboto", sans-serif;
}
.search__field {
    border: 1px solid lightgray;
    border-right: none;
}

.search__field span {
    font-size: 0.9rem;
    padding: 15px 20px;
    padding-right: 10px;
    background: white;
}
.search__field input {
    border: none;
    outline: none;
    padding: 15px 20px;
    padding-left: 0;
    color: gray;
    font-size: 0.9rem;
    font-weight: 100;
    width: 250px;
}

@media (max-width: 768px) {
    .search__field input {
        width: 200px;
    }
}
@media (max-width: 576px) {
    .search-section {
        padding: 1.5rem 1rem;
    }
    .search-section {
        flex-direction: column;
    }
    .search__field {
        border-right: 1px solid lightgray;
    }
    .search__btn .btn-yellow {
        width: 100%;
    }
}

.venue {
    padding: 1rem 3rem;
}
.venue__title {
    border: 1px solid #333;
    border-right: none;
    border-left: none;
    text-transform: uppercase;
    font-size: 1.5rem;
    font-family: didot-italic;
    margin: 1rem auto 3rem auto;
    width: 250px;
    text-align: center;
    padding-top: 10px;
}
.venue__row {
    display: flex;
    max-width: 1230px;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.venue__tile-50 {
    position: relative;
    cursor: pointer;
    text-decoration: none;
    color: #333;
    height: 270px;
    width: 600px;
}
.venue-1 {
    background: linen url(../../static/media/venue-1.90fd5012.jpg) no-repeat center;
    background-size: cover;
}
.venue-2 {
    background: linen url(../../static/media/venue-2.08acfb2b.jpg) no-repeat center;
    background-size: cover;
}
.tile-name {
    position: absolute;
    padding: 15px 25px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 3px;
    font-size: 1.1rem;
}

@media (max-width: 768px) {
    .venue {
        padding: 1rem 2rem;
    }
    .venue__tile-50 {
        height: 200px;
        width: 325px;
    }
    .tile-name {
        font-size: 1rem;
        letter-spacing: 2px;
    }
}

@media (max-width: 576px) {
    .venue {
        padding: 1rem;
    }
    .venue__title {
        margin-bottom: 2rem;
    }
    .venue__tile-50 {
        height: 200px;
        /* width: 300px; */
    }
    .venue__row {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .venue-1 {
        margin-bottom: 1rem;
    }
    .tile-name {
        font-size: 0.9rem;
        letter-spacing: 1px;
        padding: 10px 20px;
    }
}

/*
    SECOND ROW
-------------------*/

.venue__row-2-left {
    display: flex;
    justify-content: space-between;
    min-width: 600px;
}
.venue__tile-25 {
    position: relative;
    height: 250px;
    width: 275px;
    text-decoration: none;
    color: #333;
}
.venue-3 {
    background: linen url(../../static/media/venue-3.a50e937f.jpg) no-repeat center;
    background-size: cover;
}
.venue-4 {
    background: linen url(../../static/media/venue-4.3e0f9e37.jpg) no-repeat center;
    background-size: cover;
}

.venue__row-2-right {
    display: flex;
    justify-content: center;
    width: 600px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: space-around;
}
.venue__row-2-right-title {
    text-align: center;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 3px;
}
.venue__row-2-right-col {
    display: flex;
    justify-content: space-evenly;
}
.venue__links {
    display: flex;
    flex-direction: column;
}
.venue__links a {
    text-decoration: none;
    color: black;
    margin: 5px;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    text-transform: lowercase;
    text-align: center;
}
.venue__links a.venue__links__cta {
    text-decoration: underline;
    font-weight: 700;
    letter-spacing: 1px;
}
@media (max-width: 768px) {
    .venue__row-2 {
        flex-direction: column;
    }
    .venue__tile-25 {
        height: 200px;
        width: 325px;
    }
    .venue__row-2-right {
        width: 100%;
        margin-top: 1rem;
    }
}
@media (max-width: 576px) {
    .venue__row-2-left {
        flex-direction: column;
        align-items: center;
    }
    .venue__tile-25 {
        margin-bottom: 1rem;
    }
}

/*
    HOVER EFFECTS
*/
.venue-1:hover,
.venue-2:hover,
.venue-3:hover,
.venue-4:hover {
    transition: 0.3s ease;
}
.venue-1:hover,
.venue-2:hover,
.venue-3:hover,
.venue-4:hover {
    opacity: 0.8;
}

.venue-1:hover .tile-name,
.venue-2:hover .tile-name,
.venue-3:hover .tile-name,
.venue-4:hover .tile-name {
    background: linen;
    opacity: 1;
}

.venue__links a:hover {
    color: rgb(180, 135, 75);
}

.vendors {
    padding: 1rem 3rem;
}
.vendors__title {
    text-align: center;
    text-transform: uppercase;
    font-size: 1.5rem;
    font-family: didot-italic;
    width: 150px;
    margin: 0 auto 1.5rem auto;
    position: relative;
}
.vendors__title::before {
    width: 500px;
    height: 1px;
    background: lightgray;
    content: "";
    display: block;
    position: absolute;
    top: 40%;
    right: 100%;
}
.vendors__title::after {
    width: 500px;
    height: 1px;
    background: lightgray;
    content: "";
    display: block;
    position: absolute;
    top: 40%;
    left: 100%;
}

.vendors__container {
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
}
.vendors__item {
    height: 300px;
    width: 300px;
    padding: 10px;
    position: relative;
}
.vendors__item a {
    color: #333;
    text-decoration: none;
}
.vendors__item__title {
    position: absolute;
    padding: 15px 25px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 3px;
    font-size: 1.1rem;
}
.vendors__item__content {
    background: linen;
    height: 100%;
    width: 100%;
    cursor: pointer;
    transition: 0.3s ease;
}
.vendor2,
.vendor4,
.vendor9 {
    width: 600px;
}
.vendors__item__content1 {
    background: url(../../static/media/vendor-1.09ea9487.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content2 {
    background: url(../../static/media/vendor-2.d21b6ead.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content3 {
    background: url(../../static/media/vendor-3.6d454d56.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content4 {
    background: url(../../static/media/vendor-4.90605550.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content5 {
    background: url(../../static/media/vendor-5.1f0874d8.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content6 {
    background: url(../../static/media/vendor-6.e67efacf.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content7 {
    background: url(../../static/media/vendor-7.b65e3a98.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content8 {
    background: url(../../static/media/vendor-8.ed4a50bd.jpg) no-repeat center;
    background-size: cover;
}
.vendors__item__content9 {
    background: url(../../static/media/vendor-9.c375b4b7.jpg) no-repeat center;
    background-size: cover;
}

@media (max-width: 768px) {
    .vendors {
        padding: 1rem 2rem;
    }
    .vendor2,
    .vendor4 {
        width: 300px;
    }
    .vendor9 {
        width: 600px;
    }
    .vendors__title::before {
        width: 200px;
    }
    .vendors__title::after {
        width: 200px;
    }
}

@media (max-width: 576px) {
    .vendors__title::before {
        width: 100px;
    }
    .vendors__title::after {
        width: 100px;
    }
    .vendors {
        padding: 1rem;
    }
    .vendors__item {
        width: 100%;
    }
}

/*
    HOVER EFFECT
*/

.vendors__item__content:hover {
    opacity: 0.8;
}
.vendors__item__content1:hover + .vendors__item__title,
.vendors__item__content2:hover + .vendors__item__title,
.vendors__item__content3:hover + .vendors__item__title,
.vendors__item__content4:hover + .vendors__item__title,
.vendors__item__content5:hover + .vendors__item__title,
.vendors__item__content6:hover + .vendors__item__title,
.vendors__item__content7:hover + .vendors__item__title,
.vendors__item__content8:hover + .vendors__item__title,
.vendors__item__content9:hover + .vendors__item__title {
    background: linen;
    opacity: 1;
}

